<template>
  <el-form label-position="top" :model="createParams" :rules="rules" ref="createform">
    <el-row :gutter="20">
      <el-col :span="6">
        <el-form-item prop="user_name">
          <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="createParams.photo" :src="createParams.photo" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col :span="18">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item prop="user_name" label="">
              <el-input v-model="createParams.user_name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item prop="id_card" label="">
              <el-input v-model="createParams.id_card"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item prop="sex" label="">
                <el-radio-group v-model="createParams.sex">
                  <el-radio :label="0"></el-radio>
                  <el-radio :label="1"></el-radio>
                </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item prop="mobile" label="">
              <el-input v-model="createParams.mobile"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="nation" label="">
          <el-input v-model="createParams.nation"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="native_place" label="">
          <el-input v-model="createParams.native_place"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="specialty" label="">
          <el-input v-model="createParams.specialty"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="education" label="">
          <el-input v-model="createParams.education"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
export default {
  name: 'CreateEmployeeVisible',
  props: ['architectures_id'],
  data () {
    return {
      createParams: {
        user_name: '', // 
        id_card: '', // 
        sex: '', // 
        mobile: '', // 
        nation: '', // 
        native_place: '', // 
        photo: '', // 
        specialty: '', // 
        education: '', // 
        architectures_id: '' // id
      },
      rules: {
        user_name: [
          { required: true, message: '' }
        ]
      }
    }
  },
  methods: {
    handleAvatarSuccess (res, file) {
      this.createParams.photo = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    },
    submit () {
      console.log(this)
      return new Promise((resolve, reject) => {
        this.$refs.createform.validate((valid) => {
          if (valid) {
            this.$api.enterprise.employee.create({
              ...this.createParams,
              architectures_id: this.architectures_id
            }).then(() => {
              resolve()
            }).catch(() => {
              reject(new Error(''))
            })
          }
        })
      })
    }
  }
}
</script>

